
  .form-div {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
  }
  
  .student-details form label {
    display: block;
    font-weight: bold;
  }
  .student-details form input {
    flex: 1;
    padding: 8px;
    border-radius: 4px;
    border: 1px solid #ccc;
    font-size: 16px;
    box-sizing: border-box;
  }

  /* Table cell styling */
  td {
    padding: 12px;
    border: 1px solid #ddd;
    text-align: left;
  }
  
  /* Hover effect on rows */
  tr:hover {
    background-color: #e5e5e5;
  }
 