  .body{
        
        margin: 10px;
        padding: 5px;
  }
  .navbar {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #d6aae4;
    padding: 10px;
  }


  input {
    padding: 6px;
    background-color: azure;
    border-radius: 4px;
  }
  
  button {
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    background-color: #9c9da5;
    margin: 5px;
    cursor: pointer;
  }
  
  @media screen and (max-width: 850px) {
    .navbar {
      flex-direction: column;
      align-items: flex-start;
    }
    
    .search-button {
      margin-top: 10px;
    }
  }
  table {
    width: 100%;
    border-collapse: collapse;
    /* margin-bottom: 20px; */
  }

  /* Header row styling */
  th {
    background-color: #f2f2f2;
  }

  /* Alternate row coloring */
  tr:nth-child(even) {
    background-color: #f9f9f9;
  }

  /* Table cell styling */
  td, th {
    padding: 12px;
    border: 1px solid #ddd;
    text-align: left;
  }

  /* Hover effect on rows */
  tr:hover {
    background-color: #e5e5e5;
  }
  
  